// Title Bar

// wrapper
div.listTitles {
    border-bottom: double $dark-gray-color 4px;
    display: grid;
    align-items: end;
    padding: 0 1rem 1rem;
}
tr.listTitles {
    border-bottom: double $dark-gray-color 4px;

    th {
        padding-bottom: 1rem;
        text-align: left;
    }
}

// items
.listTitle {
    @include primary-font();
    line-height: 1;
    letter-spacing: normal;
    font-size: 1rem;
}
// /end Title Bar
